export default {
  data () {
    return {}
  },
  methods: {
    doJump () {
      const { query } = this.$route
      let redirect = decodeURI(query.redirect)
      let next = ''
      if (redirect.indexOf('?') === -1) {
        next = `${redirect}?token=${query.token}`
      } else {
        next = `${redirect}&token=${query.token}`
      }
      location.href = next
    }
  },
  mounted () {
    this.doJump()
  }
}
